import React, { Component } from 'react';
import logo_restaurant from './logo_restaurant.svg'
import './Reset.css';
import './App.css';

const Header = props => (
  <div className="header_div">
    <img src={logo_restaurant} alt="logo" className="logo_img"/> 
    <p className="headertxt">Recipe App</p>
  </div>
)

const SearchForm = props => (
  <form className="searchForm_div">
    <div>
      <div className='input-field'>
        <input className="validate" type='text' name='search' id='search' placeholder="search..." value={props.searchVal} onChange={props.handleChange}/>
      </div>
    </div>
    <button className="btn" onClick={props.selectRecipes}>search</button>
  </form>
 
)

const DirectoryView = props => (
  <div className="directoryView_div">
    <SearchForm handleChange={props.handleChange} searchVal={props.searchVal} selectRecipes={props.selectRecipes}/>
    <div>
      {props.selectedRecipes.map((e, i) => (<RecipeCard showDetails={props.showDetails} selectedRecipes={e.name} key={i}/>))}
    </div>

  </div>
)

const RecipeCard = props => (
  // <div className="recipeCard_div"> 
    <div className="recipeCard_div" onClick={props.showDetails}>{props.selectedRecipes}</div>
  // </div>
)

const DataList = props => (<dd>{props.counter}- {props.details} </dd>);
const ShowImage = props => {
  console.log(props.selectedForDetails.img)
  try{
    return <img src={require(''+props.selectedForDetails.img)} alt="recipe" className="recipe_img"/>
  }catch(err){
    console.log(err)
    return <img src={require('./img/img_plc.png')} alt="recipe" className="recipe_img"/>
  }

}

const DetailView = props => (
  <ul className="detailView_div">
    <div className="detailView_header">
      <ShowImage selectedForDetails={props.selectedForDetails}/>
      <div><h3>{props.selectedForDetails.name}</h3></div>
    </div>
    <dl>
      <div>
        <dt><h5>Ingredients:</h5></dt>
        {props.selectedForDetails.ingredients.map((e, i) => (<DataList counter={i+1} details={e} key={i}/> ))} 
      </div>
      <div>
        <dt><h5>Instructions:</h5></dt>
        <ol>
          {props.selectedForDetails.instructions.map((e, i) => (<DataList counter={i+1} details={e} key={i}/>))}
        </ol>
        
      </div>
    </dl>
  </ul>

)

const Details = props => {
  if (Object.keys(props.selectedForDetails).length !== 0) {
    return <DetailView selectedForDetails={props.selectedForDetails}/>;
  }
  return <div></div>;
}


class App extends Component {
  state = {
    selectedRecipes: [],
    searchVal: '',
    selectedForDetails: {},
    list: [{
      id: 1,
      name: 'TURKEY + STUFF',
      ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
      instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery'],
      img: './img/turkey_1.png'
    },
    {
      id: 2,
      name: 'TURKEY SANDWICH',
      ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
      instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery'],
      img: './img/turkey_2.png'
    },
    {
      id: 3,
      name: 'MOZARELLA TURKEY PIZZA',
      ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
      instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery'],
      img: './img/pizza.png'
    },
    {
      id: 4,
      name: 'FISH TACOS',
      ingredients: ['fish', 'tortillas', 'onions', 'bellpeppers', 'guacamole'],
      instructions: ['pre-heat over to 350', 'cut some onions', 'cook'],
      img: './img/fish.png'
    },
    {
      id: 5,
      name: 'SPANAKOPITA',
      ingredients: ['Olive oil', 'Onion', 'Garlic', 'Spinach'],
      instructions: ['Put the stuff in a bowl', 'cut some onions', 'Cook the stuff'],
      img: './img/spanakopita.png'
    }
    
  ]  
  }

  handleChange = (event) => {
    this.setState({searchVal: event.target.value});
  }

  selectRecipes = (event) => {
    event.preventDefault();
    const selection = this.state.list.filter(e => (e.name.toUpperCase()).includes((this.state.searchVal).toUpperCase()));
    this.setState({selectedRecipes: selection , searchVal: ''});
  }

  showDetails = (event) => {
    event.preventDefault();
    const selection = this.state.list.filter(e => e.name.includes(event.target.innerHTML));
    this.setState({selectedForDetails: selection[0]});
  }


  render() {
    return (
      <div className=" App">
        <Header/>
        <DirectoryView handleChange={this.handleChange} searchVal={this.state.searchVal} selectRecipes={this.selectRecipes} selectedRecipes={this.state.selectedRecipes} showDetails={this.showDetails}/>
        <Details selectedForDetails={this.state.selectedForDetails} />

        {/* <DetailView className="col s6" selectedForDetails={this.state.selectedForDetails}/> */}
      </div>
    );
  }
}


export default App;
